<template>
  <div class="item">
    <input type="checkbox" v-model=todo.complete>
    <div class="content">
      {{todo.title}}
    </div>
    <div class="btn" @click="deleterItem">删除</div>
  </div>
</template>
<script>
export default {
  props:{
    todo:Object,
    index:Number,
    deleterTodo:Function
  },
  methods:{
    deleterItem(){
      this.deleterTodo(this.index);
    }
  }
  
}
</script>
