<template>
  <div id="app">
    <!-- <todoheader class="header" :addTodo='addTodo'></todoheader> -->
    <!-- 绑定addtodo事件监听 -->
    <todoheader class="header" @addTodo='addTodo'></todoheader>
    <todolist class="list" :todos='todos' :deleterTodo="deleterTodo"></todolist>
    <todofooter class="footer" :todos="todos" :deletercom="deletercom" :selectAlltodo="selectAlltodo"></todofooter>
  </div>
</template>

<script>
import TodoHeader from './components/TodoHeader.vue'
import TodoList from './components/TodoList.vue'
import TodoFooter from './components/TodoFooter.vue'
import stroageUtill from './util/stroageUtill.js'
export default {
  data(){
    return {
      // todos: [
      //   {title: '吃饭',complete: false},
      //   {title: '睡觉',complete: true},
      //   {title: 'coding',complete: false}
      // ]
      // todos: JSON.parse(window.localStorage.getItem('todos-key') || '[]')
    todos:stroageUtill.readTodos()
    }
  },
  watch: {
    todos: {
      deep:true,
      handler: function(value){
        // window.localStorage.setItem('todos-key',JSON.stringify(value));
        stroageUtill.saveTodos(value)
      }
    }
  },
  components:{
    "todoheader": TodoHeader,
    'todolist': TodoList,
    'todofooter': TodoFooter,
  },
  methods:{
    addTodo(todo){
      this.todos.unshift(todo)
    },
    deleterTodo(index){
      this.todos.splice(index,1)
    },
    deletercom(){
      this.todos=this.todos.filter(todo =>{
        return !todo.complete
      })
    },
    selectAlltodo(check){
      this.todos.forEach(todo =>{
        todo.complete=check
      })
    }
  }
}
</script>

