<template>
  <div class="header">
    <input type="text" placeholder="请输入你的任务名称,按回车键确认"
    @keyup.enter="addItem" v-model="title">
  </div>
</template>
<script>
export default {
  name:'TodoHeader',
  props: {
    // addTodo: Function
  },
  data(){
    return{
      title: ''
    }
  },
  methods:{
    addItem(){
      //检查输入的合法性，根据输入生成一个todo对象，添加到todos，清除输入。
      let inputcontent=this.title.trim();
      let todo={
        title:inputcontent,
        complete:false
      };
      if(!inputcontent){
        alert("必须输入");
        return
      }
      
      // this.addTodo(todo);
      // 调用事件
      this.$emit('addTodo',todo)
      this.title='';
      console.log(this.title);
    }
  }
}
</script>
<style scoped>
input{
  width: 100%;
  height: 2.5em;
  border-radius: 0.5em;
  outline: none;
  text-indent: 1em;
  font-size: 1em;
  border: 2px solid #eee;;
}
</style>
