//  使用LocalStorage存储数据的工具模块
// 1、函数
// 2、对象
// 需要向外暴露1个功能还是多个功能（1个就用函数，多个就用对象）
const TODOS_KEY = 'todos-key'
export default {
  saveTodos (todos) {
    window.localStorage.setItem(TODOS_KEY, JSON.stringify(todos))
  },
  readTodos () {
    return JSON.parse(window.localStorage.getItem(TODOS_KEY) || '[]')
  }
}
