<template>
  <div class="footer">
    <input type="checkbox" v-model="isAllCheck">
    <div class="span">
      <span>已完成{{completesize}}</span> / <span>全部{{todos.length}}</span>
    </div>
    <button v-show="completesize" @click="deletercom">清除已完成任务</button>
  </div>
</template>
<script>
export default {
  props:{
    todos: Array,
    deletercom: Function,
    selectAlltodo:Function
  },
  computed:{
    // completesize (){
    //  return this.todos.reduce((arr,todo) =>{
    //     arr+(todo.complete?1:0)
    //   },0);
      
    // }
    completesize:function(){
       return this.todos.reduce((preTotal,todo) => preTotal + (todo.complete?1:0),0)
    },
    isAllCheck:{
      get() {
        return this.completesize===this.todos.length && this.completesize>0;
      },
      set(value){
        this.selectAlltodo(value);
      }
    }
  }
}
</script>
<style scoped>
.footer{
  width: 100%;
  height: 35px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}
.footer div{
  display: inline-block;
}
.footer button{
  padding: 1px 10px;
  background-color: #42b983;
  position: absolute;
  right: 2px;
}
.footer input{
  width: 20px;
  height: 20px;
  margin-left: 1em;
  border: 1px solid #eee;
}
</style>
